import React from 'react';

import styles from "./privacy.module.scss"

export default function Privacy() {
    return (
        <>
            <section className="section">
                <div className="container">
                    <div className="columns">
                        <div className={`column is-10 is-offset-1 ${styles.container}`}>
                            <h1>Privacy Policy</h1>
                            <p>This Privacy Policy describes to you how Cliqtick Social Media Management collects, shares, protects and uses your personal information.</p>
                            <p>Users who use this website agree to this Privacy Policy. We strongly encourage them to visit this page regularly to stay informed of its contents and changes.</p>
                            <p>Cliqtick Social Media Management is referred to in this policy as “we, us and site.”</p>
                            <h2>This Privacy Policy is applicable to</h2>
                            <p>The following policy describes how cliqtick.com collects and shares information with you. In other distribution channels, such as email, social media etc.</p>
                            <h2>Information Use</h2>
                            <p>For the purpose of better serving your needs, Cliqtick has a policy of collecting and storing personal information that you choose to voluntarily provide on our contact forms and in the course of corresponding with us. We collect your name, your email address, your phone number. Additionally, the website may also automatically collect information, such as your IP address, browser type, operating system, referring URLs, and actions taken on the site, to enhance your experience.Our site may use this information to offer you a better user experience, improve our site for better customer service, send periodic emails that are relevant to your needs and respond to inquiries, questions and requests.</p>
                            <p>Our commitment to preserving your privacy ensures that your information is utilized only in the most beneficial manner. We do not rent, sell or share personal information about you with non-affiliated third parties.</p>
                            <ul>
                                <li>In order to provide the services or products you want</li>
                                <li>To provide the information to trusted entities working on our behalf who commit to strict confidentiality agreements in order to help us communicate with you about relevant offers from Cliqtick Social Media Management and our marketing partners. The companies above do not have any independent rights to distribute or use this information.</li>
                                <li>In the event of subpoenas, court orders, or other legal process, a lawyer may be called upon to demonstrate or defend legal rights or defend legal claims.</li>
                                <li>To be shared for the purpose of investigating, preventing, or taking action against any illegal activities, suspected fraud, situations involving potential threats to the physical safety of any person or as otherwise required by law</li>
                                <li>Should Cliqtick Social Media Management be acquired by or merged with another company, your information will be transferred to the other company.</li>
                            </ul>
                            <h2>Protected Information</h2>
                            <p>In order to protect you against unauthorized access to, alteration, disclosure, or destruction of your personal information, we follow industry best practices when collecting, storing, and processing personal information.</p>
                            <h2>Cookies</h2>
                            <p>The site may use “cookies” to enhance your user experience. Web browsers generate cookies to keep records and track information regarding your web browsing. Cookie permission can be altered in your personal settings. The functionality of the site may be impacted if you set your web browser to refuse cookies, or to alert you when cookies are being sent. Besides security purposes, cookies are used to keep track of page visits, downloads, and users' paths.</p>
                            <h2>Other Website Links</h2>
                            <p>Links to external websites may be provided on this site as a way to provide additional content or services. If you click on this link, you will leave Cliqtick’s site. Privacy practices at other sites may differ from ours. We make no endorsements or representations regarding third-party sites. Whenever you leave Cliqtick's site, we strongly encourage you to review the privacy policies on the other sites to ensure they meet your individual standards and preferences.</p>
                            <h2>Children’s Online Privacy Protection Act compliance</h2>
                            <p>Protecting the privacy of children is extremely important. The content of the site was never intended to appeal to people under the age of 13.</p>
                            <h2>It's Your Information, Your Choices</h2>
                            <p>If you choose no longer to receive Cliqtick Social Media Management's email alerts, you can unsubscribe from receiving them at any time. Your information provided to us may be updated, modified, or deleted if needed by contacting us.</p>
                            <h2>Changes to this privacy statement</h2>
                            <p>This Privacy Policy may be updated at any time by Cliqtick Social Media Management. In order to keep you informed about policy changes, we strive to keep users informed about any changes to the policy. Prior versions of this policy are kept for records. Please review this page frequently for any possible updates to stay informed on how we protect your personal information.</p>
                            <h2>The following terms are subject to your acceptance.</h2>
                            <p>By using our site, you agree to abide by this policy. If you don't agree, you should not use our site. If you use this site in the future or if it is updated, you are deemed to accept any changes to this policy.</p>
                            <h2>Contacting us</h2>
                            <p>For any questions on this site or our Privacy Policy, please contact us through our Contact Form. You can also contact us at the following address:</p>
                            <a href="http://maps.google.com/?q=Cliqtick Marketing, KIMS Hospital Road, Sheelanagar, Visakhapatnam, Andhra Pradesh" target="_blank">
                            <p>Door No. 32-10-8 Sheelanagar</p>
                            <p>Venkateswara colony, Street no. 8</p>
                            <p>KIMS Hospital Road</p>
                            <p>Visakhapatnam, Andhra Pradesh, India - 530012</p></a>
                            <p>support@cliqtick.com</p>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}
